.sidebar {
  flex: 1;
  height: calc(100vh - 50px);
  background-color: rgb(251, 251, 255);
  position: sticky;
  top: 50px;
}

.sidebar-wrapper {
  padding: 20px;
  color: #555;
}

.sidebar-menu {
  margin-bottom: 10px;
}

.sidebar-title {
  font-size: 13px;
  color: rgb(187, 186, 186);
}

.sidebar-list {
  list-style-type: none;
  padding: 5px;
}

.sidebar-list-item {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.sidebar-list-item.active, 
.sidebar-list-item:hover {
  background-color: rgb(228, 228, 250);
}

.sidebar-icon {
  margin-right: 5px;
  font-size: 20px !important;
}

.link {
  text-decoration: none;
  color: black;
}